import React, { useState, useEffect } from 'react'
import {
	Card,
	CardBody,
	CardHeader,
	Col,
	Row,
	Form,
	InputGroup,
	InputGroupAddon,
	Input,
	InputGroupText,
	Button,
	FormFeedback,
	Alert,
} from "reactstrap";
import { handleValidation, validations } from '../../utils/validations';
import { useInput, useSubmit } from '../../hooks/form';
import useAlert from '../../hooks/useAlert';
import DatePicker from "react-datepicker";
import { apiPost } from '../../utils/serviceManager';
import { backendRoutes, messages } from '../../utils/constants';
import Select from 'react-select'



const customStyles = {
	option: (provided, state) => ({
		...provided,
		borderBottom: '1px dotted pink',
		color: state.isSelected ? 'red' : 'blue',
		padding: 20,
	}),
	// control: () => ({
	//   // none of react-select's styles are passed to <Control />
	//   width: '200px',
	// }),
	singleValue: (provided, state) => {
		const opacity = state.isDisabled ? 0.5 : 1;
		const transition = 'opacity 300ms';

		return { ...provided, opacity, transition };
	}
}


export default function EditCampaign(props) {
	let name, description;
	console.log("Selected campaign data: ", props.location.state)
	const campaignData = props.location.state.data
	name = useInput("name", campaignData.name, handleValidation, validations.EMPTY);
	description = useInput("description", campaignData.description, handleValidation, validations.EMPTY);

	const selectedAdsArr = campaignData.ads ? campaignData.ads.map((data, key) => {
		return (
			{ label: data.name, value: data.ad_id }
		)
	}) : []

	const [startDate, setStartDate] = useState(new Date(campaignData.start_time))
	const [endDate, setEndDate] = useState(new Date(campaignData.end_time))
	const [adOptions, setAdOptions] = useState([])
	const [selectedAds, setSelectedAds] = useState(
		selectedAdsArr
	)

	const alert = useAlert(false);
	const submit = useSubmit([name, description], handleSuccess);

	function handleSuccess(formData) {
		formData = { ...formData, start: startDate.toISOString(), end: endDate.toISOString(), campaign_id: campaignData.campaign_id }
		console.log("Data Length==> ", formData);
		editCampaign(formData)
		// name.clearInput();
		// description.clearInput();
		// setStartDate(new Date())
		// setEndDate(new Date())
	}

	function editCampaign(formData) {
		apiPost(
			backendRoutes.CAMPAIGN + 'update',
			{ data: formData },
			{
				"Content-Type": "application/json",
				//Authorization: `Bearer ${localStorage.getItem("token")}`,
			},
			successRes => {
				console.log("campaign edit success: ", successRes);
				alert.changeVisibility(true);
				alert.handleMessage("campaign edit successfully");
				setTimeout(() => {
					alert.changeVisibility(false);
				}, 5000);
			},
			errorRes => {
				console.log("campaign edit error: ", errorRes);
				if (errorRes.response && errorRes.response.data) {
					alert.handleMessage(errorRes.response.data.message);
				} else {
					alert.handleMessage(messages.NETWORK_FAILURE);
				}
				setTimeout(() => {
					alert.changeVisibility(false);
				}, 5000);
			}
		)
	}

	function handleAlertVisibility() {
		alert.changeVisibility(false);
	}

	function handleStartDate(date) {
		console.log("Start date selected: ", date.toISOString())
		setStartDate(date)
	}

	function handleEndDate(date) {
		console.log("End date selected: ", date.toISOString())
		setEndDate(date)
	}

	useEffect(() => {
		getAds()
	}, [])

	function getAds() {
		apiPost(
			backendRoutes.ADS + 'get',
			{},
			{
				"Content-Type": "application/json",
			},
			response => {
				// console.log("Api response: ", response)
				let apiData = response.data.data;
				let optionsArr = apiData
					.map((data, index) => {
						return (
							{ label: data.name, value: data.ad_id }
						)
					})

				console.log('optionsArr: ', optionsArr)
				setAdOptions(optionsArr)

			},
			error => {
				console.log("Api error: ", error)
			}
		)
	}

	function callDetachAd(detachAdsArr) {
		apiPost(
			backendRoutes.CAMPAIGN_ADS + 'detach',
			{ ad_id: detachAdsArr, campaign_id: campaignData.campaign_id },
			{},
			successRes => {
				console.log('successRes for detach ads: ', successRes)
			},
			errorRes => {
				console.log('errorRes for detach ads: ', errorRes)
			}
		)
	}

	function callAttachAd(detachAdsArr) {
		apiPost(
			backendRoutes.CAMPAIGN_ADS + 'attach',
			{ ad_id: detachAdsArr, campaign_id: campaignData.campaign_id },
			{},
			successRes => {
				console.log('successRes for attach ads: ', successRes)
			},
			errorRes => {
				console.log('errorRes for attach ads: ', errorRes)
			}
		)
	}

	function handleSelectInput(data, event) {
		// const name = e.target.name;
		// const value = e.target.value;
		let detachAdsArr = []
		let attachAdsArr = []
		// console.log("Selected input name: ", data);
		// console.log("event of select input: ", event);
		console.log("Campaign id: ", campaignData.campaign_id);
		if (event.action === 'remove-value') {
			setSelectedAds(data)
			detachAdsArr.push(event.removedValue.value)
			console.log('detachAdsArr in remove-value: ', detachAdsArr)
			callDetachAd(detachAdsArr)
		}
		if (event.action === 'select-option') {
			setSelectedAds(data)
			attachAdsArr.push(event.option.value)
			console.log('attachAdsArr in select-option: ', attachAdsArr)
			callAttachAd(attachAdsArr)
		}
		if (event.action === 'clear') {
			detachAdsArr = selectedAds.map((stateData, key) => {
				return stateData.value
			})
			console.log('detachAdsArr in clear: ', detachAdsArr)
			callDetachAd(detachAdsArr)
		}
		// console.log("Selected input value: ", value);
		// if (name === "colors") {
		//   // setOwnerId(value);
		// }
		// if (name === "monitor") {
		//   // setMonitorId(value);
		// }
	}

	function renderAdSelect() {
		// if (campaignData.ads) {
			// let defaultOptions = []
			// defaultOptions = campaignData.ads ? campaignData.ads.map((data, key) => {
			// 	return (
			// 		{ label: data.name, value: data.ad_id }
			// 	)
			// }) : []
			return (
				<>
					<Select
						defaultValue={selectedAds}
						//styles={customStyles}
						isMulti
						name="adSelect"
						options={adOptions}
						className="basic-multi-select"
						classNamePrefix="select"
						onChange={handleSelectInput}
						placeholder='Select ads for the campaign.'
					/>
					<p></p>
				</>
			)
		// }
		// else {
		// 	return (
		// 		<p>
		// 			loading...
		// 		</p>
		// 	)
		// }
	}

	return (
		<div className="animated fadeIn">
			<Row>
				<Col lg={6}>
					<Card>
						<CardHeader>
							<strong><i className="icon-info pr-1"></i>Campaign id: {campaignData.campaign_id}</strong>
						</CardHeader>
						<CardBody>
							<Alert
								color="danger"
								isOpen={alert.isVisible}
								toggle={handleAlertVisibility}
							>
								{alert.message}
							</Alert>
							<Form noValidate {...submit.props}>
								<h1>Edit campaign</h1>
								<p className="text-muted"></p>
								<InputGroup className="mb-3">
									<InputGroupAddon addonType="prepend">
										<InputGroupText>
											<i className="icon-user"></i>
										</InputGroupText>
									</InputGroupAddon>
									<Input
										type="text"
										placeholder="Name"
										autoComplete="on"
										{...name.props}
									/>
									<FormFeedback>{name.helperText}</FormFeedback>
								</InputGroup>
								<InputGroup className="mb-3">
									<InputGroupAddon addonType="prepend">
										<InputGroupText>
											<i className="icon-info"></i>
										</InputGroupText>
									</InputGroupAddon>
									<Input
										type="text"
										placeholder="Description"
										autoComplete="off"
										{...description.props}
									/>
									<FormFeedback>{description.helperText}</FormFeedback>
								</InputGroup>
								<InputGroup className="mb-3">
									<InputGroupAddon addonType="prepend">
										<InputGroupText>
											<i className="icon-calendar"></i>
										</InputGroupText>
									</InputGroupAddon>
									<DatePicker
										selected={startDate}
										onChange={handleStartDate}
										showTimeSelect
									//dateFormat="ISO"
									/>
								</InputGroup>
								<InputGroup className="mb-3">
									<InputGroupAddon addonType="prepend">
										<InputGroupText>
											<i className="icon-calendar"></i>
										</InputGroupText>
									</InputGroupAddon>
									<DatePicker
										selected={endDate}
										onChange={handleEndDate}
										showTimeSelect
									//dateFormat="ISO"
									/>
								</InputGroup>
								{/* <InputGroup className="mb-3"> */}
								{/* <InputGroupAddon addonType="prepend">
										<InputGroupText>
											<i className="icon-calendar"></i>
										</InputGroupText>
									</InputGroupAddon> */}
								{renderAdSelect()}
								{/* </InputGroup> */}
								<Button color="success" block>
									Edit Campaign
                </Button>
							</Form>
						</CardBody>
					</Card>
				</Col>
			</Row>
		</div>
	)
}
